export const introContentEn = {
    title: "Eros, as a Modern Punishment",
    para1: "In South Korean society, freedom is suppressed by relationships. Individuals fetishize their own certainties and advantages, ignorant of other people as infinity. In a relationship only an individuals system or calculation matters. Eros is destroyed. Eros is pure desire towards the other, and is a passion for life which allows us to suffer the changes and sacrifices caused by the other. To follow this desire, it is necessary to be opposed to the virtue wherein the society has been rooted. As such, the works which manifest our bodies being changed by other peoples bodies appear to be beautiful. Yet this phenomenon itself is a punishment for someone.",
    para2: "Performance/Exhibition is run through local art spaces and a website. It focuses on intimate communication with local audiences. Meanwhile, during todays pandemic, performance video, not as a replacement but as a part of holistic performance, is live-streamed on SNS. The different roles of the local and the global are newly connected. This trial is based on the artists pursuit of a transdisciplinary performance-image."
}

